<template>
  <div class="overdue">
    <div class="left_menu">
      <!--  默认选中第一个渠道，站点，国家，hover显示 新增列（状态） status_name -->
      <el-form :model="formData">
        <el-form-item label="渠道">
          <el-dropdown
            @command="handleCommand_channel"
            class="overdue_dropdown"
          >
            <div>
              <el-input
                v-model="formData.channel"
                placeholder="请选择渠道"
              ></el-input>
              <i class="el-icon-arrow-down el-icon--right"></i>
            </div>
            <el-dropdown-menu class="overdue_dropdownmenu" slot="dropdown">
              <el-dropdown-item
                :command="item"
                v-for="(item, i) in channels"
                :key="i"
                >{{ item }}</el-dropdown-item
              >
            </el-dropdown-menu>
          </el-dropdown>
        </el-form-item>

        <el-form-item label="国家">
          <el-dropdown
            @command="handleCommand_country"
            class="overdue_dropdown"
          >
            <div>
              <el-input
                v-model="formData.country"
                placeholder="请选择国家"
              ></el-input>
              <i class="el-icon-arrow-down el-icon--right"></i>
            </div>

            <el-dropdown-menu class="overdue_dropdownmenu" slot="dropdown">
              <el-dropdown-item
                :command="item"
                v-for="(item, i) in countries"
                :key="i"
                >{{ item }}</el-dropdown-item
              >
            </el-dropdown-menu>
          </el-dropdown>
        </el-form-item>
        <el-form-item label="站点">
          <el-dropdown
            @command="handleCommand_station"
            class="overdue_dropdown"
          >
            <div>
              <el-input
                v-model="formData.station"
                placeholder="请选择站点"
              ></el-input>
              <i class="el-icon-arrow-down el-icon--right"></i>
            </div>
            <el-dropdown-menu class="overdue_dropdownmenu" slot="dropdown">
              <el-dropdown-item
                :command="item"
                v-for="(item, i) in stations"
                :key="i"
                >{{ item }}</el-dropdown-item
              >
            </el-dropdown-menu>
          </el-dropdown>
        </el-form-item>
        <el-form-item label="仓库名">
          <el-dropdown
            @command="handleCommand_warehousename"
            class="overdue_dropdown"
          >
            <div>
              <el-input
                v-model="formData.warehouse_name"
                placeholder="请选择仓库名"
              ></el-input>
              <i class="el-icon-arrow-down el-icon--right"></i>
            </div>
            <el-dropdown-menu class="overdue_dropdownmenu" slot="dropdown">
              <el-dropdown-item
                :command="item"
                v-for="(item, i) in warehouse_names"
                :key="i"
                >{{ item }}</el-dropdown-item
              >
            </el-dropdown-menu>
          </el-dropdown>
        </el-form-item>
        <el-form-item label="时间">
          <el-date-picker
            v-model="formData.date"
            value-format="yyyy-MM-dd"
            type="date"
            placeholder="选择日期"
          >
          </el-date-picker>
        </el-form-item>
        <el-form-item label="海外仓">
          <el-dropdown
            @command="handleCommand_warehouse"
            class="overdue_dropdown"
          >
            <div>
              <el-input
                v-model="formData.warehouse"
                placeholder="请选择海外仓"
              ></el-input>
              <i class="el-icon-arrow-down el-icon--right"></i>
            </div>

            <el-dropdown-menu class="overdue_dropdownmenu" slot="dropdown">
              <el-dropdown-item
                :command="item"
                v-for="(item, i) in warehouses"
                :key="i"
                >{{ item }}</el-dropdown-item
              >
            </el-dropdown-menu>
          </el-dropdown>
        </el-form-item>
        <el-form-item label="快递商">
          <el-dropdown
            @command="handleCommand_logisticsupplier"
            class="overdue_dropdown"
          >
            <div>
              <el-input
                v-model="formData.logistic_supplier"
                placeholder="请选择仓库"
              ></el-input>
              <i class="el-icon-arrow-down el-icon--right"></i>
            </div>
            <el-dropdown-menu class="overdue_dropdownmenu" slot="dropdown">
              <el-dropdown-item
                :command="item"
                v-for="(item, i) in logistic_suppliers"
                :key="i"
                >{{ item }}</el-dropdown-item
              >
            </el-dropdown-menu>
          </el-dropdown>
        </el-form-item>
        <el-form-item label="订单号">
          <el-input
            v-model="formData.Amazon_id"
            placeholder="请输入订单号"
          ></el-input>
        </el-form-item>
        <el-form-item label="问题归因">
          <el-dropdown
            @command="handleCommand_question_reason"
            class="overdue_dropdown"
          >
            <div>
              <el-input
                v-model="formData.question_reason"
                placeholder="请选择问题归因"
              ></el-input>
              <i class="el-icon-arrow-down el-icon--right"></i>
            </div>

            <el-dropdown-menu class="overdue_dropdownmenu" slot="dropdown">
              <el-dropdown-item
                :command="item"
                v-for="(item, i) in question_reasons"
                :key="i"
                >{{ item }}</el-dropdown-item
              >
            </el-dropdown-menu>
          </el-dropdown>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="search" class="search"
            >搜索</el-button
          >
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="clearSearch" class="search"
            >清空选项</el-button
          >
        </el-form-item>
        <!-- <el-form-item>
          <el-button
            type="primary"
            @click="go_route('/DeliveryTimeMaintenance')"
            >配送时间维护</el-button
          >
        </el-form-item> -->
      </el-form>
    </div>
    <div class="right_con">
      <div class="site_div" style="color:#fff" v-if="this.country">
        <span>国家：{{ country }}</span>
        <span>站点：{{ station }}</span>
      </div>
      <div class="spanBlock" style="font-size:14px;background:#fff">
        <span
          @click="clickSpan(i, item)"
          :class="['status' + i, { clickSpan: curIndex === i }]"
          v-for="(item, i) in question_types"
          :key="i"
        >
          {{ item }}
        </span>
        <span
          :class="['copyCol', { copyActive: copyClick ? true : false }]"
          style="position:relative;"
          @click="copyColData"
          >点击复制追踪编码列
          <input
            style="position:absolute;top:0;left:0;opacity:0;z-index:-999"
            type="text"
            v-model="str"
          />
        </span>
      </div>
      <el-table
        ref="overdueTable"
        highlight-current-row
        @cell-click="showOrderInfo"
        :cell-style="cellStyle"
        border
        :data="tableData"
        style="width: 100%"
        v-loading="loading"
        element-loading-text="拼命加载中"
        element-loading-spinner="el-icon-loading"
        element-loading-background="rgba(0, 0, 0, 0.3)"
        max-height="700"
      >
        <el-table-column
          type="index"
          label="序号"
          width="60"
          :index="indexMethod"
        >
        </el-table-column>
        <el-table-column prop="dates" label="下单日期" width="160">
        </el-table-column>
        <!-- <el-table-column prop="station" label="站点" width="60">
        </el-table-column>
        <el-table-column prop="country" label="国家" width="60">
        </el-table-column> -->
        <el-table-column prop="Amazon_id" label="订单号" width="180">
        </el-table-column>
        <!-- <el-table-column prop="warehouse" label="海外仓" width="100">
        </el-table-column> -->
        <el-table-column prop="warehouse_name" label="仓库名称" width="100">
        </el-table-column>
        <!-- <el-table-column prop="warehouse_code" label="仓库编码" width="100">
        </el-table-column> -->
        <el-table-column prop="logistic_supplier" label="快递商" width="100">
        </el-table-column>
        <el-table-column prop="tracking_no" label="追踪编号" width="180">
          <!-- 点击显示物流信息 -->
          <!-- <template slot-scope="scope">
            <span
              title="点击前往官网"
              style="display:block;width:100%;height:100%;text-decoration:underline;color:blue;cursor:pointer"
              >{{ scope.row.tracking_no }}</span
            >
            <input
              @click="
                showLink(scope.row.logistic_supplier, scope.row.tracking_no)
              "
              class="toLink"
              style="cursor:pointer;position: absolute;width: 100%;height: 100%;top: 0;left: 0;opacity: 0;"
              type="text"
              v-model="scope.row.tracking_no"
            />
          </template> -->
        </el-table-column>
        <el-table-column prop="sku" label="sku" width="180"> </el-table-column>
        <!-- <el-table-column prop="product_name" label="品名" width="180">
        </el-table-column> -->
        <el-table-column prop="question_type" label="问题类型" width="100">
        </el-table-column>

        <el-table-column prop="all_days" label="实际用时" width="120">
        </el-table-column>

        <el-table-column prop="actual_date" label="实际到达日期" width="160">
        </el-table-column>

        <el-table-column prop="expected_date" label="预计送达日期" width="160">
        </el-table-column>

        <el-table-column prop="overtime_time" label="超时时间" width="140">
        </el-table-column>
        <el-table-column prop="question_reason" label="问题归因" width="100">
        </el-table-column>
        <!-- status_name -->
        <el-table-column label="状态" width="100">
          <template slot-scope="scope">
            <el-button @click="showDialog(scope.row)" size="mini"
              >未处理</el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @current-change="changePage"
        :current-page.sync="currentPage"
        :page-size="50"
        layout="total, prev, pager, next,jumper"
        :total="totalNum"
      >
      </el-pagination>
      <el-dialog
        class="orderQuery"
        title="订单查询"
        :visible.sync="dialogVisible1"
        width="40%"
      >
        <div
          id="orderquery"
          class="orderquery"
          style="background: rgba(255, 255, 255, 0.9)"
        >
          <div class="topMenu">
            <el-form
              label-width="100px"
              :inline="true"
              class="demo-form-inline"
            >
              <el-form-item label="跟踪编号">
                <el-input v-model="tracking_no" :disabled="true"></el-input>
              </el-form-item>
              <el-form-item label="物流仓">
                <el-input v-model="supplier" :disabled="true"></el-input>
              </el-form-item>
            </el-form>
            <div
              class="links"
              style="padding-bottom: 10px;
    text-indent: 7em;"
            >
              <a
                href="https://www.17track.net/zh-cn/"
                style="color:blue;text-decoration:underline;"
                target="_blank"
                >点击前往17</a
              >

              <a
                :href="alink"
                style="color:blue;text-decoration:underline;"
                target="_blank"
                >点击前往官网</a
              >
            </div>
            <!-- <el-button @click="go_route('order_tracing')" type="primary"
              >返回</el-button
            > -->
          </div>
          <div
            class="detail"
            style="max-height:500px;overflow:auto;padding-left: 95px;"
          >
            <label>订单详情</label>
            <div class="orderdetail" v-show="flag">
              <el-steps space="30" direction="vertical">
                <el-step
                  icon="el-icon-circle-check"
                  status="process"
                  v-for="(item, index) in order_details"
                  :key="index"
                  :title="item.time"
                  :description="item.content"
                >
                </el-step>
              </el-steps>
            </div>
            <div v-show="!flag">
              <p>{{ msg }}</p>
            </div>
          </div>
        </div>
      </el-dialog>
    </div>
    <!-- 弹框 -->
    <el-dialog title="提示" :visible.sync="due_dialogVisible" width="30%">
      <span>是否确认提交此未处理订单</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="dueOrder">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  computed: {
    alink() {
      let link = "";
      switch (this.supplier) {
        case "DHL":
          link = "https://track.dhlparcel.co.uk/";
          break;
        case "UPS":
          // link = "https://www.ups.com/cn/en/Home.page";
          link = " https://www.ups.com/track?loc=en_CN&requester=ST/";
          break;
        case "DPD":
          link = "https://tracking.dpd.de/status/gb_GB/404";
          break;
        case "GLS":
          link = "https://gls-group.eu/GROUP/en/parcel-tracking";
          break;
        case "FedEx":
          link = "https://www.fedex.com/zh-cn/home.html";
          break;
      }
      return link;
    }
  },
  data() {
    return {
      copyClick: false,
      str: "",
      // 订单查询
      dialogVisible1: false,
      order_details: [],
      flag: false, // 显示flag
      // alink: "",
      supplier: "",
      tracking_no: "",

      orderId: "",

      msg: "",
      //
      loading: false,
      currentPage: 1,
      totalNum: 0,
      formData: {
        channel: "",
        station: "",
        country: "",
        warehouse_name: "",
        date: "",
        warehouse: "",
        logistic_supplier: "",
        question_type: "",
        question_reason: ""
      },
      tableData: [],
      channels: [],
      stations: [],
      countries: [],
      warehouse_names: [],
      warehouses: [],
      logistic_suppliers: [],
      question_types: [],
      question_reasons: [],
      allSelectData: [], // 渠道&&站点&&国家
      otherList: [], // 仓库名&&仓库&&国家
      due_dialogVisible: false,
      currentDueOrder: "",
      // alink: "", // 前往官网链接
      country: "",
      station: "",
      curIndex: ""
    };
  },
  methods: {
    copyColData() {
      this.copyClick = !this.copyClick;
      if (this.copyClick) {
        // 选中
        let item = document.querySelector(".copyCol>input");
        item.select();
        document.execCommand("Copy");
      }
    },
    cellStyle({ row, column, rowIndex, columnIndex }) {
      if (columnIndex === 5) {
        return {
          color: "blue",
          cursor: "pointer",
          textDecoration: "underline"
        };
      }
    },
    showOrderInfo(row, col) {
      console.log(row);
      if (col.label === "追踪编号") {
        this.dialogVisible1 = true;
        this.orderId = row.Amazon_id;
        this.supplier = row.logistic_supplier;
        this.tracking_no = row.tracking_no;
        let str = row.logistics_information.replace(/\'/g, '"');
        // for(let key in this.)
        str = JSON.parse(str);
        for (let key in str) {
          let item = {};
          item["time"] = str[key]["time"];
          item["content"] = str[key]["status"] + str[key]["place_name"];
          this.$set(this.order_details, key, item);
        }
        // this.order_details = JSON.parse(str);
        if (this.order_details.length > 0) {
          this.flag = true;
        } else {
          this.flag = false;
        }
      }
    },
    clickSpan(i, item) {
      console.log(i, item);
      this.curIndex = i;
      this.formData.question_type = this.question_types[i];
      // 发送请求
      this.search();
    },
    clearSearch() {
      for (let key in this.formData) {
        this.formData[key] = "";
      }
    },
    // showLink(supplier, tracking_no) {
    //   let copyTextArr = document.querySelectorAll(".toLink");
    //   let idx = this.tableData.findIndex(i => i.tracking_no === tracking_no);
    //   let copyText = copyTextArr[idx];
    //   copyText.select();
    //   document.execCommand("Copy");
    //   console.log(supplier);
    //   switch (supplier) {
    //     case "FedEx":
    //       this.alink = "https://www.fedex.com/zh-cn/home.html";
    //       break;
    //     case "UPS":
    //       this.alink = "https://www.ups.com/track?loc=en_CN&requester=ST/";
    //       break;
    //     case "DHL":
    //       link = "https://track.dhlparcel.co.uk/";
    //       break;
    //     case "DPD":
    //       link = "https://tracking.dpd.de/status/gb_GB/404";
    //       break;
    //     case "GLS":
    //       link = "https://gls-group.eu/GROUP/en/parcel-tracking";
    //       break;
    //   }
    //   console.log(supplier, this.alink);
    //   if (this.alink) {
    //     window.open(this.alink);
    //   }
    // },
    showDialog(item) {
      this.currentDueOrder = item;
      this.due_dialogVisible = true;
    },
    handleCommand_channel(command) {
      this.formData.channel = command;
    },
    handleCommand_country(command) {
      this.formData.country = command;
      this.changeOpt();
    },
    handleCommand_station(command) {
      this.formData.station = command;
    },
    handleCommand_warehousename(command) {
      this.formData.warehouse_name = command;
    },
    handleCommand_warehouse(command) {
      this.formData.warehouse = command;
    },
    handleCommand_logisticsupplier(command) {
      this.formData.logistic_supplier = command;
    },
    handleCommand_question_type(command) {
      this.formData.question_type = command;
    },
    handleCommand_question_reason(command) {
      this.formData.question_reason = command;
    },
    dueOrder() {
      this.$axios
        .put(this._url2 + "server/order_management/get_timeout_order/", {
          id: this.currentDueOrder.id
        })
        .then(res => {
          if (res.data.code === 200) {
            // 刷新页面
            location.reload();
          } else {
            alert("请求未成功");
          }
        });
    },
    go_route(val) {
      this.$router.push(val);
    },
    changePage(val) {
      this.currentPage = val;
      // 请求
      this.getAllSelect();
    },
    indexMethod(index) {
      return index + 1 + 50 * (this.currentPage - 1);
    },
    getSelect() {
      let url1 = "server/personnel_management/get_sign_country/";
      this.$axios.get(this._url2 + url1).then(res => {
        this.channels.push(res.data.data1);
        this.formData.channel = this.channels[0];
        this.countries = res.data.data;
        this.formData.country = this.countries[0];
        this.changeOpt(this.getAllSelect);
      });
      let url = "server/order_management/get_search_data/";
      this.$axios.get(this._url2 + url).then(res => {
        // this.allSelectData = res.data.data;
        this.otherList = res.data.warehouse_list; // 仓库&&国家
        this.question_types = res.data.question_type_list;
        this.question_reasons = res.data.question_reason_list;
      });
    },
    // 追踪编号复制
    initCopy() {
      this.str = "";
      for (let key in this.tableData) {
        let item = this.tableData[key];
        for (let value in item) {
          if (value === "tracking_no") {
            this.str += item[value] + ",";
          }
        }
      }
      this.str = this.str.replace(/\,+$/g, "");
      console.log("订单追踪编码", this.str);
    },
    getAllSelect() {
      this.loading = true;
      let url1 = "server/order_management/get_timeout_order/";
      let queryObj = {};
      for (let key in this.formData) {
        queryObj[key] = this.formData[key];
      }
      queryObj["page"] = this.currentPage;
      this.$axios
        .get(this._url2 + url1, {
          params: queryObj
        })
        .then(res => {
          this.tableData = res.data.data;
          this.$nextTick(() => {
            this.$refs["overdueTable"].bodyWrapper.scrollTop = 0;
          });
          this.copyClick = false;
          this.initCopy();
          this.totalNum = res.data.re_count_data[0].count_id;
          this.loading = false;
          if (this.tableData.length > 0) {
            let item = this.tableData[0];
            this.country = item.country;
            this.station = item.station;
          }
        });
    },
    changeOpt(cb) {
      this.stations = [];
      this.formData.station = "";
      this.warehouse_names = [];
      this.formData.warehouse_name = "";
      if (this.formData.country !== "") {
        let url = this._url2 + "server/personnel_management/get_sign_area/";
        this.$axios
          .get(url, {
            params: {
              country: this.formData.country
            }
          })
          .then(res => {
            this.stations = res.data.data;
            this.formData.station = this.stations[0];
            if (cb && typeof cb === "function") {
              cb();
            }
          });

        this.otherList.forEach(i => {
          if (i.country === this.formData.country) {
            this.warehouse_names.push(i.warehouse_name);
          }
        });
      }
    },
    go_route(val) {
      this.$router.push(val);
    },
    search() {
      this.currentPage = 1;
      let paramsObj = {};
      for (let key in this.formData) {
        let item = this.formData[key];
        // paramsObj.append(key, item);
        paramsObj[key] = item;
      }
      let url = "server/order_management/get_timeout_order/";
      this.$axios
        .get(this._url2 + url, {
          params: paramsObj
        })
        .then(res => {
          this.tableData = res.data.data || [];
          this.totalNum = res.data.re_count_data[0].count_id;
          this.$nextTick(() => {
            this.$refs["overdueTable"].bodyWrapper.scrollTop = 0;
          });
          if (this.tableData.length > 0) {
            // 获取国家站点
            this.copyClick = false;
            this.initCopy();
            console.log(this.tableData);
            let item = this.tableData[0];
            this.country = item.country;
            this.station = item.station;
          }
        });
    }
  },
  created() {
    this.getSelect();
    // this.getAllSelect();
  }
};
</script>

<style scoped>
.copyActive {
  border: 1px solid;
}
.overdue {
  display: flex;
  padding: 30px 10px;
  align-items: flex-start;
}
.overdue >>> .el-form-item__label {
  color: #fff;
}
.left_menu {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  width: 250px;
  text-align: right;
}
.left_menu >>> .el-form-item__label {
  width: 80px;
  text-align: right;
}
.right_con {
  flex: 1;
  padding: 0 33px;
  height: 100%;
  overflow: hidden;
}
.overdue .clickSpan {
  border: 1px solid;
}
.overdue .spanBlock {
  padding: 10px 0;
}
.overdue .spanBlock > span:hover {
  border: 1px solid;
  cursor: pointer;
}
.overdue .spanBlock > span {
  margin: 0 30px;
  position: relative;
  display: inline-block;
  height: 100%;
  box-sizing: border-box;
  padding-right: 40px;
  padding-left: 10px;
}

.overdue .spanBlock > span::after {
  content: "";
  bottom: 8px;
  right: 10px;
  position: absolute;
  width: 15px;
  height: 100%;
}
.status0::after {
  border-bottom: 4px solid #f56c6c;
}
.status1::after {
  border-bottom: 4px solid #c06113;
}
.status2::after {
  border-bottom: 4px solid #e92727;
}
</style>

<style>
.overdue .left_menu .el-form-item {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
}
.overdue .right_con .el-pagination {
  background: #fff;
}
.overdue .el-form--inline .el-form-item__label {
  width: 80px;
}
.overdue .left_menu .el-form-item .search {
  width: 125px;
}
.overdue .el-date-editor.el-input {
  width: 200px;
}
.overdue .left_menu .el-form-item__content {
  line-height: 40px;
  position: relative;
  font-size: 14px;
  width: 150px;
  overflow: hidden;
  border-radius: 5px;
}
.overdue .el-icon--right {
  margin-left: 5px;
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
  color: #c0c4cc;
}

.overdue_dropdownmenu {
  width: 150px;
  text-align: center;
  max-height: 300px;
  overflow-y: auto;
}
.overdue_dropdownmenu::-webkit-scrollbar {
  width: 5px;
  height: 5px;
  background-color: #f5f5f5;
}
.overdue_dropdownmenu::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  background-color: #f5f5f5;
}
</style>



